textarea {
	width: 100%;
}

input.input-text.is-invalid {
	@include prefix( box-shadow, 0px 0px 5px rgba(#e2401c, .6 ), o moz webkit );
}

.form-inline {
	align-items: stretch;
	justify-content: center;

	input {

		&:focus {
			@include prefix( box-shadow, 0 1px 6px 0 rgba( #52abf6, .7 ), webkit moz o);
		}
	}
}